
@import url("https://fonts.googleapis.com/css?family=Karla");
html, body {
  height: 100.1%;
}
#app {
  font-family: 'Karla', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  margin: 0 auto;
}
body, p {
  font-family: 'Karla', Helvetica, Arial, sans-serif;
  font-size: 20px;
  line-height: 1.25em;
  font-weight: lighter;
  color: #7C7C7C;
  padding: 0;
  margin: 0;
}
p {
  margin-top: 0;
  margin-bottom: 1.35em;
}
a:link, a:hover, a:visited, a:active {
  color: #329AE9;
  text-decoration: none;
}
.wrapper {
  width: 85%;
  margin: 0 auto;
}
@media only screen and (min-width: 992px) {
.wrapper {
      width: 100%;
      max-width: 960px;
}
}
h2 {
  font-weight: lighter;
  letter-spacing: -.02em;
  margin-bottom: .25em;
  font-size: 1.4em;
  line-height: 1em;
  margin-top: 1.3em;
}
@media only screen and (min-width: 992px) {
h2 {
      font-size: 2.2em;
}
}
h2.page_title {
    font-size: 1em;
    font-weight: bold;
    text-transform: lowercase;
    letter-spacing: .01em;
    margin-bottom: 3em;
}
h2.intro {
    font-size: 2em;
    margin: 2em 0;
}
@media only screen and (min-width: 992px) {
h2.intro {
        max-width: 80%;
}
}
h3 {
  font-weight: 400;
  font-size: 1.2em;
  margin-bottom: .2em;
  margin-top: 2em;
  line-height: 1.2em;
}
footer {
  border-top: 1px solid #eee;
  margin-top: 2em;
}
footer h3 {
    margin-bottom: 0;
}
footer p {
    font-size: 15px;
    line-height: 1.4em;
    margin-bottom: .35em;
}
footer .copyright {
    margin-top: 2em;
    margin-bottom: 2em;
    font-size: 12px;
    color: #bbbbbb;
}
img {
  max-width: 100%;
  height: auto;
}
img.feature {
    width: 100%;
    height: auto;
}
.inline-list {
  display: inline-block;
  list-style-type: none;
  margin-left: 0;
  padding-left: 0;
  margin-top: 0;
}
.inline-list li {
    display: inline;
    margin-right: .5em;
}
.inline-list li:last-child {
      margin-right: 0;
}
.icon-list .icon {
  width: 24px;
  height: auto;
  opacity: .4;
  transition: opacity .2s;
}
.icon-list .icon:hover {
    opacity: .6;
}
@media only screen and (min-width: 992px) {
.slat {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: 1em 0;
}
.slat .column-half {
      width: 50%;
      padding-right: 2em;
}
.slat .column-half:last-child {
        padding-right: 0;
}
.slat .column-half p {
        font-size: .9em;
        line-height: 1.35em;
}
}
.flex-grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.flex-grid .column-third {
    width: 28%;
    text-align: center;
    margin-bottom: 1em;
}
@-webkit-keyframes slideInFromLeft {
0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
100% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
}
@keyframes slideInFromLeft {
0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
100% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
}
.custom-enter-active, .custom-leave-active {
  transition-property: opacity;
  transition-duration: .25s;
  -webkit-animation: .5s ease-out 0s 1 slideInFromLeft;
          animation: .5s ease-out 0s 1 slideInFromLeft;
}
.custom-enter-active {
  transition-delay: .25s;
}
.custom-enter, .custom-leave-active {
  opacity: 0;
}



